import type { BlockToolConstructable } from '@editorjs/editorjs';

class Tool implements BlockToolConstructable { };

export default function Editor(props) {
  function startEditor() {
    try {
      var editor = new EditorJs({
        holder: 'editorjs',
        autofocus: true,
        tools: {
          header: {
            class: Header,
            inlineToolbar: ['link'],
          },
          list: {
            class: List,
            inlineToolbar: true,
          },
          // image: {
          //   class: ImageTool,
          //   config: {
          //     endpoints: {
          //       byFile: 'http://localhost:8008/uploadFile', // Your backend file uploader endpoint
          //       byUrl: 'http://localhost:8008/fetchUrl', // Your endpoint that provides uploading by Url
          //     },
          //   },
          // },
        },
        // data: props.editorjs === null ? {} : props.editorjs,
        /**
         * onReady callback
         */
        onReady: () => {
          console.log('Editor.js is ready to work!');
        },
      });

      editor.isReady
        .then(() => {
          console.log('Editor.js is ready to work!');
          /** Do anything you need after editor initialization */
        })
        .catch((reason) => {
          console.log(`Editor.js initialization failed because of ${reason}`);
        });
    } catch {
      console.log('an error happened :(');
    }
  }
  // function editorSave() {
  //   if (props.editorjsData) {
  //     props.editorjsData.save();
  //     return redirect('/admin');
  //   }
  // }
  return (
    <>
      <div id="editorjs"></div>
      <button onClick={startEditor}>initialize editor</button>
      {/* <button onClick={editorSave()}>Save Data</button> */}
    </>
  );
}
